<template>
  <div class="container1">
    <h2>新闻分析概览</h2>      
    <!-- 第一行 -->
    <div class="row" id="zoushi">
      <!-- 热度走势折线图 -->
      <div class="title">博主历史新闻热度</div>
      <div class="trend item">
        <div class="chart" id="trend">
          <!-- echart图 -->
        </div>
      </div>
    </div>
    <!-- 第二行 -->
    <div class="row" id="pie">
       <!-- 情感占比饼图 -->
      <div class="title">评论情感分析</div>
      <div class="emotion-box">
        <div class="emotion-po item">
            <!-- {{authorId}}
            {{female}} -->
          <div class="chart" id="emotion-po">
            <!-- echart图 -->
          </div>
          <div class="po">正面文本</div>
        </div>
       <!-- 情感占比饼图 -->
        <div class="emotion-ne item">
          <div class="chart" id="emotion-ne">
            <!-- echart图 -->
          </div>
          <div class="po">负面文本</div>
        </div>
      </div>
    </div>
    <!-- 第三行 -->
    <div class="row" id="people">
      <!-- 性别比例人像图 -->
      <div class="title">评论者性别分析</div>
      <div class="item sex" >
          
        <div class="chart" id="sex_pro" ref="sexChart" >
            <!-- echart图 -->
        </div>
        <div class="prop" style="clolor:#fff" >
            <span class="man" style="display:none">{{mprop}}%</span><span class="female" style="display:none">{{feprop}}%</span>
        </div>
        
      </div>
    </div>
    <!-- 第四行 -->
    <div class="row" id="Cloudword">
      <div class="title">新闻词云</div>
      <div class="item words">
        <div class="chart" id="words_pro" style="width: 100%;
            height: 95%;">
            <!-- echart图 -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//导入echart元素
require("echarts-liquidfill")
require("echarts-wordcloud")
export default {

  data() {
    return {
      trendChart:null,
      em_posChart:null,
      em_neChart:null,
      sexChart:null,
      commentMsg:{},
      authorId:'',
      female:'',
      male:'',
      allNum:'',
      feprop:'',
      mprop:'',
      oldWeibo: [],
      converData:[],
      cloudData:{
          value:[],
          image:"data:image/png;base64,"
      },
      cloudArr:[],
      commentneg:'',
      commentother:'',
      nerate:'',
      porate:''
    };
  },
  updated(){
      
    this.drawLine()  
    // console.log('评论情感处理',this.commentneg);

    this.updataChart()

  },

  mounted() {
      setInterval(() => {
        //   this.authorId = localStorage.getItem('author_id_id');
          this.oldWeibo = this.$store.state.history_weibo
          this.female = parseFloat(localStorage.getItem('f'));
          this.male = parseFloat(localStorage.getItem('m'));
          this.allNum = this.male + this.female;
          this.feprop = (100*this.female / this.allNum ).toFixed(2)
          this.mprop = (100*this.male / this.allNum ).toFixed(2)
          this.cloudData.value = this.$store.state.weibo_word_cloud
          this.commentneg = this.$store.state.commentneg
          this.commentother = this.$store.state.commentother
          this.nerate = ((this.commentneg/(this.commentother+ this.commentneg))*100).toFixed(2);
          this.porate = ((this.commentother/(this.commentother+ this.commentneg))*100).toFixed(2);
      },2000)
      
   
       
    this.drawLine () 
    // this.getData()
    this.updataChart() 

    window.addEventListener('resize',this.resizeChart)
        
  },
  
  destroyed () {
    window.removeEventListener('resize'.this.resizeChart);
    clearInterval(this.oldWeibo,this.female,this.male,this.allNum,this.feprop,this.mprop)
  },
  methods: {
    
    drawLine() {
      // 折线图
      this.trendChart = this.$echarts.init(document.querySelector('#trend'))
      const initoption1 = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        lineStyle: {
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                    offset: 0,
                                    color: 'rgba(0, 255, 233,0)'
                                }, {
                                    offset: 0.5,
                                    color: 'rgba(255, 255, 255,1)',
                                }, {
                                    offset: 1,
                                    color: 'rgba(0, 255, 233,0)'
                                }],
                                global: false
                            }
                        },
                    },
                },
                grid: {
                    top: '15%',
                    left: '5%',
                    right: '5%',
                    bottom: '1%',
                    containLabel: true,
                },
                xAxis: [{
                    type: 'category',
                    axisLine: {
                        show: true
                    },
                    splitArea: {
                        // show: true,
                        color: '#f00',
                        lineStyle: {
                            color: '#f00'
                        },
                    },
                    axisLabel: {
                        color: '#A9AFC7'
                    },
                    splitLine: {
                        // show: falses
                        show: true,
                        lineStyle: {
                        color: ['#fff'],
                        opacity: 0.06
                        }
                    },
                    boundaryGap: false,
                    data: ["-5","-4","-3","-2","-1","预测"],

                }],

                yAxis: [{
                    type: 'value',
                    min: 0,
                    // max: 140,
                    splitNumber: 4,
                    splitLine: {
                        show: true,
                        lineStyle: {
                        color: ['#fff'],
                        opacity: 0.06
                        }
                    },
                    axisLine: {
                        show: true,
                    },
                    
                    axisLabel: {
                        show: true,
                        margin: 20,
                        textStyle: {
                        fontSize: 10,
                        color: '#A9AFC7'
                        }
                    },
                    axisTick: {
                        show: true,
                    },
                }],
                
                
      }

      this.trendChart.setOption(initoption1)
      //环形图正面
      this.em_posChart = this.$echarts.init(document.querySelector('#emotion-po'))
  
      let empoData = this.porate/100;
    //   console.log('正面/负面',this.porate,this.nerate)
      const initoption2 = {
          series: [{
                  type: 'liquidFill',
                  radius: '100%',
                  center: ['50%', '50%'],
                  data: [empoData, empoData, empoData], // data个数代表波浪数
                  backgroundStyle: {
                      borderWidth: 1,
                      color: 'transparent'
                  },
                  label: {
                      normal: {
                          formatter: this.porate + '%',
                          textStyle: {
                              fontSize: 25
                          }
                      }
                  },
                  
                  outline: {
                      show: false,
                  }
              },
              {
                  "type": "pie",
                  "center": ["50%", "50%"],
                  "radius": ["80%", "82%"],
                  "hoverAnimation": false,
                  "data": [{
                          "name": "",
                          "value": 500,
                          labelLine: {
                              show: false
                          },
                          itemStyle: {
                              color: '#5886f0'
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: '#5886f0'
                              },
                          }
                      },
                      { //画中间的图标
                          "name": "",
                          "value": 4,
                          labelLine: {
                              show: false
                          },
                          itemStyle: {
                              color: '#ffffff',
                              "normal": {
                                  "color": "#5886f0",
                                  "borderColor": "#5886f0",
                                  "borderWidth": 20,
                                  // borderRadius: '100%'
                              },
                          },
                          label: {

                              borderRadius: '100%'
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: '#5886f0'
                              },
                          }

                      },
                      { // 解决圆点过大后续部分显示明显的问题
                          "name": "",
                          "value": 4,
                          labelLine: {
                              show: false
                          },
                          itemStyle: {
                              color: '#5886f0'
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: '#5886f0'
                              },
                          }
                      },
                      { //画剩余的刻度圆环
                          "name": "",
                          "value": 88,
                          itemStyle: {
                              color: '#050038'
                          },
                          "label": {
                              show: false
                          },
                          labelLine: {
                              show: false
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: 'rgba(255,255,255,0)'
                              },
                          }
                      }
                  ]
              }

          ]
      }
      this.em_posChart.setOption(initoption2)
      //环形图负面
      this.em_neChart = this.$echarts.init(document.querySelector('#emotion-ne'))
      let emneData = this.nerate/100;
      const initoption3 = {
          series: [{
                  type: 'liquidFill',
                  radius: '100%',
                  center: ['50%', '50%'],
                  data: [emneData, emneData, emneData], // data个数代表波浪数
                  backgroundStyle: {
                      borderWidth: 1,
                      color: 'transparent'
                  },
                  label: {
                      normal: {
                          formatter: this.nerate + '%',
                          textStyle: {
                              fontSize: 25
                          },
                          color: '#fff'
                      }
                  },
                  
                  outline: {
                      show: false,
                  }
              },
              {
                  "type": "pie",
                  "center": ["50%", "50%"],
                  "radius": ["80%", "82%"],
                  "hoverAnimation": false,
                  "data": [{
                          "name": "",
                          "value": 500,
                          labelLine: {
                              show: false
                          },
                          itemStyle: {
                              color: '#5886f0'
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: '#5886f0'
                              },
                          }
                      },
                      { //画中间的图标
                          "name": "",
                          "value": 4,
                          labelLine: {
                              show: false
                          },
                          itemStyle: {
                              color: '#ffffff',
                              "normal": {
                                  "color": "#5886f0",
                                  "borderColor": "#5886f0",
                                  "borderWidth": 20,
                                  // borderRadius: '100%'
                              },
                          },
                          label: {

                              borderRadius: '100%'
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: '#5886f0'
                              },
                          }

                      },
                      { // 解决圆点过大后续部分显示明显的问题
                          "name": "",
                          "value": 4,
                          labelLine: {
                              show: false
                          },
                          itemStyle: {
                              color: '#5886f0'
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: '#5886f0'
                              },
                          }
                      },
                      { //画剩余的刻度圆环
                          "name": "",
                          "value": 88,
                          itemStyle: {
                              color: '#050038'
                          },
                          "label": {
                              show: false
                          },
                          labelLine: {
                              show: false
                          },
                          emphasis: {
                              labelLine: {
                                  show: false
                              },
                              itemStyle: {
                                  color: 'rgba(255,255,255,0)'
                              },
                          }
                      }
                  ]
              }

          ]
          

          

      }
      this.em_neChart.setOption(initoption3)
      //性别比例图
    //   this.sexChart = this.$echarts.init(document.querySelector('#sex_pro'))
      this.sexChart = this.$echarts.init(this.$refs.sexChart)
      const res = [
                {
                    "label": "男",
                    "value": this.male
                },
                {
                    "label": "女",
                    "value": this.female
                }];

const border =  'data:image/png;base64,\
    \
    y8U45QPULzIlh+YRebyGv6icyjX0s+NwkQBY+8GhqoxVBw99YESGHq\
    RVco6ve/m2uEgNDqfi2MFfoumM75BBvAxVKwB4lT3lZLotfk/ew6u4IM3s2a/chcb5vnCs\
    B09tVG+X9wEALYW78mgZLoWlW6sfiO+TospODfddunq2sKa4u1s5/xAr551gZf2zaMtYWfcC2q\
    \
    eo3e8yqzdowX7knDuY925Xv9BUW5zxTsRNCkCK5Pwpes6Jf3Os4zlBIRJMfc2or1HjX3D7QJlXpFOQzWKOAIAUnkdq\
    ';
            const man ='data:image/png;base64,';
            const woman =
                'data:image/png;base64,';
            const color = ['#00FFFF', 'rgba(196,36,255,1)'];
            const data = [];
            for (let i = 0; i < res.length; i++) {
                data.push({
                    value: res[i].value,
                    name: res[i].label,
                    itemStyle: {
                        normal: {
                            borderWidth: 5,
                            shadowBlur: 20,
                            borderColor: color[i],
                            shadowColor: color[i]
                        }
                    }
                }, {
                    value: res[0].value / 10,
                    name: '',
                    itemStyle: {
                        normal: {
                            label: {
                                show: false
                            },
                            labelLine: {
                                show: false
                            },
                            color: 'rgba(0, 0, 0, 0)',
                            borderColor: 'rgba(0, 0, 0, 0)',
                            borderWidth: 0
                        }
                    }
                });
            }
            const initoption4 = {
                color,
                graphic: {
                    elements: [{
                            type: 'image',
                            z: 3,
                            style: {
                                image: border,
                                width: 100,
                                height: 100
                            },
                            left: 'center',
                            top: 'center',
                            position: [33, 33]
                        },
                        {
                            type: 'image',
                            z: 3,
                            style: {
                                image: man,
                                width: 30,
                                height: 50
                            },
                            left: '30%',
                            top: 'center'
                        },
                        {
                            type: 'image',
                            z: 3,
                            style: {
                                image: woman,
                                width: 30,
                                height: 50
                            },
                            right: '30%',
                            top: 'center'
                        }
                    ]
                },
                tooltip: {
                    show: false
                },
                toolbox: {
                    show: false
                },
                series: [{
                    name: '',
                    type: 'pie',
                    clockWise: false,
                    radius: [58, 60],
                    hoverAnimation: false,
                    itemStyle: {
                        normal: {
                            label: {
                                show: true,
                                fontSize: 16,
                                formatter(params) {
                                    return params.name ?
                                        params.name + '\n' + params.value + '人' :
                                        '';
                                }
                            },
                            labelLine: {
                                width: 4,
                                length: 30,
                                length2: 50,
                                show: true,
                                color: '#00ffff'
                            }
                        }
                    },
                    data
                }]
            }
   
      this.sexChart.setOption(initoption4)
      //词云图
      this.wordChart = this.$echarts.init(document.querySelector('#words_pro'))
      var maskImage = new Image();
      maskImage.src = this.cloudData.image
      this.wordChart.setOption(
        {
            series: [{
              type: 'wordCloud',
             gridSize: 1,
            sizeRange: [12, 55],
            rotationRange: [-45, 0, 45, 90],

            size: ['100%', '100%'],
            // sizeRange: [5, 40],//最小文字——最大文字
            // textRotation: [0, 45, 90, -45],
            // rotationRange: [-45, 90],//旋转角度区间
            // rotationStep: 90,//旋转角度间隔
            shape: 'circle',
            // gridSize: 10,//字符间距
            // gridSize: 6,
            maskImage: maskImage,
              textStyle: {
                  normal: {
                      color: function(v) {
                          let color = ['#27D38A', '#FFCA1C', '#5DD1FA', '#F88E25','#47A0FF','#FD6565']
                          let num =Math.floor(Math.random() * (5 + 1));
                          // console.log(num)
                          return color[num];
                      },
                  },
              },
              left: 'center',
              // top: '10',
              width: '96%',
              height: '80%',
              data: this.cloudData.value
          }]
      })
    },
    
    updataChart () {
        let hotValue =[];
        this.oldWeibo.map(item => {
            hotValue.push(item.hot_value)
        })
        this.converData =(hotValue.slice(0,6).reverse())
        const trendOption = {
            series: [{
                        name: '历史热度',
                        type: 'line',
                        smooth: false, //是否平滑
                        showAllSymbol: true,
                        // symbol: 'image://./static/images/guang-circle.png',
                        symbol: 'circle',
                        symbolSize: 10,
                        lineStyle: {
                            normal: {
                                color: "#2CABE3",
                            },
                        },
                        label: {
                            show: true,
                            position: 'top',
                            textStyle: {
                                color: '#2CABE3',
                            }
                        },
                        itemStyle: {
                            color: "#fff",
                            borderColor: "#2CABE3",
                            borderWidth: 2,
                        },
                        tooltip: {
                            show: false
                        },
                        	areaStyle: { //区域填充样式
                                normal: {
                                //线性渐变，前4个参数分别是x0,y0,x2,y2(范围0~1);相当于图形包围盒中的百分比。如果最后一个参数是‘true’，则该四个值是绝对像素位置。
                                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                    { offset: 0,  color: 'rgba(61,234,255, 0.9)'}, 
                                    { offset: 1,  color: 'rgba(61,234,255, 0)'}
                                ], false),

                                shadowColor: 'rgba(53,142,215, 0.9)', //阴影颜色
                                shadowBlur: 20 //shadowBlur设图形阴影的模糊大小。配合shadowColor,shadowOffsetX/Y, 设置图形的阴影效果。
                            }
                        },
                        data: this.converData
                    }
                ]
        }
        this.trendChart.setOption(trendOption)
        // console.log(obj instanceof Object)
        
        
    },
    resizeChart () {
      this.trendChart ? this.trendChart.resize() : ''
      this.em_posChart ? this.em_posChart.resize() : ''
      this.em_neChart ? this.em_neChart.resize() : ''
      this.sexChart ? this.sexChart.resize() : ''
      this.wordChart ? this.wordChart.resize() : ''

    },
    
  },
  computed: {
    obj () {
      return this.$store.state.obj
    },
  },
};
</script>

<style lang="less" >
.container1{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 95%;
  margin: 0 auto;
  padding: 5px;
  h2{
    color:#10E7DD;
    font-size:30px;
    line-height: 1;
    margin:0 auto;
    padding:10px;
  }
  
  #zoushi {
    flex:5;
    // width:100%;
    height: 260px;
    display: flex;
    flex-direction: column;
    .item{
      width: 100%;
      height: 95%;
    }
    #trend{
      width: 100%;
      height: 100%;
      bottom: 5%;
    }
  }
  #pie {
    flex: 4;
    height: 230px;
    display: flex;
    flex-direction: column;
    .emotion-box {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      .item{
        width: 50%;
        height: 90%;
        margin: 0 auto;
        #emotion-po{
          width: 100%;
          height: 90%;
        }
        #emotion-ne{
          width: 100%;
          height: 90%;
        }
        .po{
        color:#fff;
        font-size: 18px;
        height: 10%;
        line-height: 1;
        font-weight: normal;
        display: flex;
        justify-content: center;
        padding:5px;
        }
      }
    }
    
  }
  #people {
    height: 230px;
    .sex{
        height: 100%;
        width: 100%;

        #sex_pro{
        width: 100%;
        height: 80%;
        }
        .prop{
            color:#fff;
            position: relative;
            .man{
                position: absolute;
                // margin-left: 40px;
                left: 100px;
            }
            .female{
                position: absolute;
                // margin-right: 40px;
                right: 100px;
            }
        }
        
    }
    
  }
  #Cloudword {
    height: 200px;
    .item{
        height: 100%;
        width: 100%;
        #words_pro{
            width: 100%;
            height: 95%;
        }
    }
    
  }
  .row {
    flex: 4;
    width: 100%;
     .title{
        color: #fff;
        font-size: 20px;
        font-weight: blod;
        margin: 10px 0px 10px 0px;
      }     
    .item {
      height: 90%; 
      margin : 0 auto;
      .chart {
       
        width: 100%;
        height: 80%;
      }
    }
    
  }
  
  
}
</style>