  methods: {
    ColorType(value) {
      if (value == "负面") {
        return "#FC502B";
      } else {
        return "#67B2FF";
      }
    },

    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    make_report() {
      this.$router.push("/manage/report");
    },
    chooseNew(e) {
      // console.log(e)
      const {
        author_id_id,
        author_id,
        author_avatar,
        raw_text,
        author_gender,
        weibo_id,
        hot_comments,
        history_weibo,
        weibo_word_cloud,
        comment_word_cloud,
        comment_emotion_rate,
      } = e;
      const { f, m } = e.gender;
      const { neg, other } = e.comment_emotion_rate;
      let author = {
        author_id,
        author_id_id,
        author_avatar,
        raw_text,
        author_gender,
        weibo_id,
        f,
        m,
        hot_comments,
        history_weibo,
        weibo_word_cloud,
        comment_word_cloud,
        neg,
        other,
      };
      console.log(author);
      this.$store.commit("setNickName", author);
      this.$store.commit("setAuthor", author);
      this.$store.commit("setAvatar", author);
      this.$store.commit("setText", author);
      this.$store.commit("setGender", author);
      this.$store.commit("setHistoryWeibo", author);
      this.$store.commit("setGender", author);
      this.$store.commit("setCommit", author);
      this.$store.commit("setWeiboCloud", author);
      this.$store.commit("setCommentCloud", author);
      this.$store.commit("setCommentEmotion", author);
    },
    make_report() {
      this.$router.push("/manage/report");
    },
    //级联选择获取label
    handleMallCatergoryChange() {
      if (this.mallCatergoryCode.length != 0) {
        let arr = this.$refs["cascaderMallCatergory"].getCheckedNodes()[0]
          .pathLabels;
        this.queryInfo.level = arr[2];
        this.queryInfo.query = "";
        if (arr[2] == "全国") {
          //全国版
          //主题分类
          if (arr[0] == "全部") {
            if (this.queryInfo.aspect == "") {
              arr[1] == "正面"
                ? (this.queryInfo.label = 0)
                : (this.queryInfo.label = 1);
              let param = {
                label: this.queryInfo.label,
                offset: 0,
              };
              this.getHot20(param);
            }
          } else {
            this.queryInfo.aspect = arr[0];
            if (arr[1] != "全部") {
              //情感分类
              arr[1] == "正面"
                ? (this.queryInfo.label = 0)
                : (this.queryInfo.label = 1);
            } else {
              this.queryInfo.label = "";
            }
            let param = {
              aspect: this.queryInfo.aspect,
              label: this.queryInfo.label,
              offset: 0,
            };
            //请求hot20接口
            this.getHot20(param);
          }

          this.queryInfo.pagenum = 0;
          this.queryInfo.pagecurrent = 1;
        } else {
          //重邮版
          //主题分类
          if (arr[0] == "全部") {
            arr[1] == "正面"
              ? (this.queryInfo.label = 0)
              : (this.queryInfo.label = 1);
            let param = {
              label: this.queryInfo.label,
              offset: 0,
            };
            this.getCquptHot20(param);
          } else {
            this.queryInfo.aspect = arr[0];
            if (arr[1] != "全部") {
              arr[1] == "正面"
                ? (this.queryInfo.label = 0)
                : (this.queryInfo.label = 1);
            } else {
              this.queryInfo.label = "";
            }
            let param = {
              aspect: this.queryInfo.aspect,
              label: this.queryInfo.label,
              offset: 0,
            };
            this.getCquptHot20(param);
          }
          this.queryInfo.pagenum = 0;
          this.queryInfo.pagecurrent = 1;
        }
      }
    },

    //Hot20级联请求
    async getHot20(hot20param) {
      console.log("hot20参数", hot20param);
      const { data: ret } = await this.$url.get("news/hot20/", {
        params: hot20param,
      });
      // console.log('hot20级联请求',ret.data)
      this.tableData = ret.data;
      this.updata();
    },
    async getCquptHot20(cquptparam) {
      console.log("cqupthot20参数", cquptparam);
      const { data: ret } = await this.$url.get("news/cquptHot20/", {
        params: cquptparam,
      });
      // console.log('cqupthot20级联请求',ret.data)
      this.tableData = ret.data;
      this.updata();
    },

    //搜索初始请求
    async getNewsList() {
      this.queryInfo.pagecurrent = 1;
      const { data: ret } = await this.$url.get("news/search", {
        params: {
          q: this.queryInfo.query,
          offset: 0,
        },
      });
      this.tableData = ret.data;
      console.log('搜索',this.tableData);
      this.updata();
    },
    //监听 pagesize 改变的事件
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
    },
    //监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage - 1;
      this.queryInfo.pagecurrent = newPage;
      console.log("监听页码值", this.queryInfo.pagenum);

      if (this.queryInfo.query != "") {
        this.getSearchPagination();
      } else {
        this.getPagination();
      }
    },
    //分页请求
    getPagination() {
      console.log("参数列表", this.queryInfo);
      if (this.queryInfo.aspect == "" && this.queryInfo.label == "") {
        let pageparam = {
          offset: this.queryInfo.pagenum,
        };
        this.getHot20(pageparam);
      } else if (this.queryInfo.aspect == "" && this.queryInfo.label != "") {
        let pageparam = {
          label: this.queryInfo.label,
          offset: this.queryInfo.pagenum,
        };
        console.log("分页参数", pageparam);
        this.queryInfo.level == "全国"
          ? this.getHot20(pageparam)
          : this.getCquptHot20(pageparam);
      } else if (this.queryInfo.aspect != "") {
        //主题分类
        let pageparam = {
          aspect: this.queryInfo.aspect,
          label: this.queryInfo.label,
          offset: this.queryInfo.pagenum,
        };
        this.queryInfo.level == "全国"
          ? this.getHot20(pageparam)
          : this.getCquptHot20(pageparam);
      }
    },
    //搜索分页
    async getSearchPagination() {
      const { data: ret } = await this.$url.get("news/search", {
        params: {
          q: this.queryInfo.query,
          offset: this.queryInfo.pagenum,
        },
      });
      console.log("我真么没打印");

      // this.queryInfo.query = '';
      this.tableData = ret.data;
      // this.total = this.tableData.length;

      console.log('搜索',this.tableData);
      this.updata();
    },
    async getData() {
      const { data: ret } = await this.$url.get("news/hot20/");
      this.tableData = ret.data;
      this.total = this.tableData.length;
      this.updata();
    },
    updata() {
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].label == 1) {
          this.tableData[i].label = "负面";
        } else if (this.tableData[i].label == 0) {
          this.tableData[i].label = "正面";
        }
      }
    },
  },
};
